/* Contact */

#contact {
  background-color: var(--white);
}

.content-list {
  margin: 0 auto;
  max-width: 570px;
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 40px;
  text-align: center;
}

.content-list a {
  color: var(--accent);
}

.content-list-item {
  font-size: 18px;
  line-height: 1.5;
}

.content-list-item p + p {
  margin-top: 0.5em;
}
